import { createAsyncThunk } from '@reduxjs/toolkit';

import { selectPartialScan } from "./analyzerSlice";

import {
  setReadyToAnalyze,
  selectFiles,
  addMetaData,
  selectMetaData,
  getAudioData
} from "../file-handler/fileHandlerSlice";

import { setReadyToRender } from "../renderer/rendererSlice";

import {
  setTempo,
  setAverageTempo
} from "../parameters/parametersSlice";

const partialScanStart = 15;
const partialScanEnd = 45;

window.result = {
  peak: null,
  rms: null,
  tempo: null
};

export const calcAverageTempo = createAsyncThunk(
  'analyzer/calcAverageTempo',
  (_, { dispatch, getState }) => {
    const files = selectFiles(getState());

    if (!files.length) {
      dispatch(setTempo(120));
      dispatch(setAverageTempo(120));
    } else {
      const metaData = selectMetaData(getState());

      const tempo = Math.round(files.reduce((a,b) => a + metaData[b].tempo, 0) / files.length);
  
      dispatch(setTempo(tempo));
      dispatch(setAverageTempo(tempo));
    }
  }
);

export const callAnalyzer = createAsyncThunk(
  'analyzer/callAnalyzer',
  (_, { dispatch, getState }) => {
    const files = selectFiles(getState());
    const partialScan = selectPartialScan(getState());

    dispatch(setReadyToAnalyze(false));
    dispatch(setReadyToRender(false));

    files.forEach((file, i) => {
      const Module = window.Module;

      const numResult = 4;
      const resPtr = Module._malloc(numResult * Float32Array.BYTES_PER_ELEMENT);
      const resView = new Float32Array(Module.HEAP32.buffer, resPtr, numResult);
      resView.fill(0);

      let audioBuffer = getAudioData(file);

      if (audioBuffer.numberOfChannels === 1) {
        let data = audioBuffer.getChannelData(0);
        if (partialScan) data = data.slice(44100*5,44100*45);
  
        const ptr = Module._malloc(data.byteLength);
        const view = new Float32Array(Module.HEAP32.buffer, ptr, data.length);
        view.set(data);
  
        Module.ccall(
          'analyzeMono',
          null,
          ['number', 'number', 'number', 'number'],
          [ptr, data.length, resPtr, numResult]
        );
  
        Module._free(ptr);
      } else {
        let leftData = audioBuffer.getChannelData(0);
        let rightData = audioBuffer.getChannelData(1);

        if (partialScan) {
          leftData = leftData.slice(44100*partialScanStart, 44100*partialScanEnd);
          rightData = rightData.slice(44100*partialScanStart, 44100*partialScanEnd);
        }

        const leftPtr = Module._malloc(leftData.byteLength);
        const leftView = new Float32Array(Module.HEAP32.buffer, leftPtr, leftData.length);
        leftView.set(leftData);

        const rightPtr = Module._malloc(rightData.byteLength);
        const rightView = new Float32Array(Module.HEAP32.buffer, rightPtr, rightData.length);
        rightView.set(rightData);

        Module.ccall(
          'analyzeStereo',
          null,
          ['number', 'number', 'number', 'number', 'number'],
          [leftPtr, rightPtr, leftData.length, resPtr, numResult]
        );
  
        Module._free(leftPtr);
        Module._free(rightPtr);
      }  
  
      dispatch(addMetaData({
        file: file,
        data: Object.assign({}, window.result)
      }));

      Module._free(resPtr);
    });

    dispatch(calcAverageTempo());
    dispatch(setReadyToAnalyze(true));
    dispatch(setReadyToRender(true));
  }
);