import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

export const fileHandlerSlice = createSlice({
  name: 'fileHandler',
  initialState: {
    files: [],
    metaData: {},
    readyToAnalyze: false,
    metaDataReady: false
  },
  reducers: {
    addFile(state, { payload }) {
      state.files.push(payload);
    },
    addMetaData(state, { payload }) {
      const file = payload.file;
      const data = payload.data;
      state.metaData[file] = data;
    },
    clearFiles(state) {
      state.files = [];
      state.metaData = {};
      state.readyToAnalyze = false;
    },
    setReadyToAnalyze(state, { payload }) {
      state.readyToAnalyze = payload;
    }
  } 
});

export const {
  addFile,
  addMetaData,
  setReadyToAnalyze
} = fileHandlerSlice.actions;

// Private
const { clearFiles } = fileHandlerSlice.actions;

export const selectFiles = (state) => state.fileHandler.files;
export const selectMetaData = (state) => state.fileHandler.metaData;
export const selectReadyToAnalyze = (state) => state.fileHandler.readyToAnalyze;

// A mutable data store -- [fileName]: AudioBuffer
const audioData = {};

export function setAudioData(name, buffer) {
  audioData[name] = buffer;
}

export function getAudioData(name) {
  return audioData[name];
}

function clearAudioData() {
  Object.keys(audioData).forEach(key => {
    delete audioData[key];
  });
}

export const clearFilesAndAudioData = createAsyncThunk(
  'fileHandler',
  (_, { dispatch }) => {
    dispatch(clearFiles());
    clearAudioData();
  }
);

export default fileHandlerSlice.reducer;