import { createSlice } from '@reduxjs/toolkit';

export const analyzerSlice = createSlice({
  name: 'analyzer',
  initialState: {
    partialScan: true
  },
  reducers: {
    setPartialScan(state, { payload }) {
      state.partialScan = payload;
    }
  }
});

export const { setPartialScan } = analyzerSlice.actions;

export const selectPartialScan = (state) => state.analyzer.partialScan;

export default analyzerSlice.reducer;