import { useSelector, useDispatch } from "react-redux";
import { useEffect } from "react";

import {
  setLoudness,
  setFilterFreq,
  setTempo,
  setUseAverageTempo,
  selectLoudness,
  selectFilterFreq,
  selectTempo,
  selectUseAverageTempo,
} from './parametersSlice';

import { selectReadyToRender } from "../renderer/rendererSlice";

import { calcAverageTempo } from "../analyzer/analyzerAPI";

export function Parameters() {
  const dispatch = useDispatch();

  const loudness = useSelector(selectLoudness);
  const filterFreq = useSelector(selectFilterFreq);
  const tempo = useSelector(selectTempo);
  const useAverageTempo = useSelector(selectUseAverageTempo);
  const readyToRender = useSelector(selectReadyToRender);

  const minLoudness = -96;
  const maxLoudness = 0;
  const minFilterFreq = 0;
  const maxFilterFreq = 3000;
  const minTempo = 40;
  const maxTempo = 180;

  useEffect(() => {}, [loudness, filterFreq, tempo]);

  function onLoudnessChange(event) {
    let value = parseInt(event.target.value);
    if (isNaN(value) || value > maxLoudness || value < minLoudness) {
      return;
    } else {
      dispatch(setLoudness(value));
    }
  }

  function onFilterFreqChange(event) {
    let value = parseInt(event.target.value);
    if (isNaN(value) || value > maxFilterFreq || value < minFilterFreq) return;
    dispatch(setFilterFreq(value));
  }

  function onUseAvgTempoChange(event) {
    dispatch(setUseAverageTempo(event.target.checked));
    if (event.target.checked) {
      dispatch(calcAverageTempo());
    }
  }

  function onTempoChange(event) {
    let value = parseInt(event.target.value);
    if (isNaN(value)) return;
    if (value < minTempo) value = minTempo;
    if (value > maxTempo) value = maxTempo;
    dispatch(setTempo(value));
  }

  const parameterRow = `
    w-full py-1 space-x-4 px-4
    flex justify-between items-center  
  `;
  const parameterLabel = "w-1/5 pl-4";
  const parameterSlider = "w-1/3";
  const numberBox = "w-1/3 space-x-2";

  return (
    <div className={`
      border-4 border-solid
      ${readyToRender ? 'border-gray-400' : 'border-gray-200' }
      w-full py-2 space-y-2
      flex flex-col items-center justify-center
    `}>
      <div className={parameterRow}>
        <label className={parameterLabel} htmlFor="dbfs">
          Loudness:
        </label>
        <input className={parameterSlider} type="range" min={minLoudness} max={maxLoudness} value={loudness} onChange={onLoudnessChange} />
        <div className={numberBox}>
          <input type="number" id="dbfs" name="dbfs" value={loudness} onChange={onLoudnessChange} />
          <span>dBFS</span>
        </div>
      </div>
      <div className={parameterRow}>
        <label className={parameterLabel} htmlFor="freq">
          HPF Freq:
        </label>
        <input className={parameterSlider} type="range" min={minFilterFreq} max={maxFilterFreq} value={filterFreq} onChange={onFilterFreqChange} />
        <div className={numberBox}>
          <input type="number" id="freq" name="freq" value={filterFreq} onChange={onFilterFreqChange} />
          <span>Hz</span>
        </div>
      </div>
      <div className={parameterRow}>
        <label className={parameterLabel} htmlFor="bpm">
          Tempo:
        </label>
        <input className={parameterSlider} type="range" min={minTempo} max={maxTempo} value={tempo} disabled={useAverageTempo} onChange={onTempoChange} />
        <div className={numberBox}>
          <input type="number" id="bpm" name="bpm" value={tempo} onChange={onTempoChange} disabled={useAverageTempo} />
          <span>BPM</span>
        </div>
      </div>
      <div className={`pb-1`}>
        <input type="checkbox" id="use-avg-tempo" name="use-avg-tempo" defaultChecked={useAverageTempo} onChange={onUseAvgTempoChange} />
        <label htmlFor="use-avg-tempo">Use the average tempo of the tracks</label>
      </div>
    </div>
  );
}