import { useDispatch } from "react-redux";
import { useRef, useState } from "react";

import {
  setReadyToAnalyze,
  clearFilesAndAudioData
} from "./fileHandlerSlice";

import { processFiles } from "./fileHandlerAPI";

import { setReadyToRender } from "../renderer/rendererSlice";
import { resumeAudioContext } from "../renderer/rendererAPI";

export function FileLoader() {
  const dispatch = useDispatch();
  const fileInputRef = useRef();
  
  async function onFileInput(event) {
    dispatch(clearFilesAndAudioData());
    dispatch(setReadyToRender(false));
    await dispatch(resumeAudioContext(true));
    await dispatch(processFiles(event.target?.files));
    dispatch(setReadyToAnalyze(true));
  }

  async function onFileDrop(event) {
    event.preventDefault();

    dispatch(clearFilesAndAudioData());
    dispatch(setReadyToRender(false));
    await dispatch(resumeAudioContext(true));

    const files = event.dataTransfer.files;

    if (!files.length) {
      alert("Oops! Could not capture the files (it may be a browser bug). Please try again and it might work...")
    } else {
      await dispatch(processFiles(files));
      dispatch(setReadyToAnalyze(true));
    }
  }

  function invokeFileUpload(event) {
    fileInputRef.current.click();
    event.stopPropagation();
  }

  const [borderHighlight, setBorderHighlight] = useState(false);

  return (
    <div 
      className={`
        w-full py-8
        border-4 border-solid
        ${borderHighlight ? 'border-gray-400' : 'border-gray-200'}
        flex flex-col justify-center items-center
      `}
      onDrop={onFileDrop} 
      onDragOver={event => event.preventDefault()}
      onClick={invokeFileUpload}
      onMouseEnter={() => setBorderHighlight(true)}
      onMouseLeave={() => setBorderHighlight(false)}
    >
      <input 
        type="file" 
        id="file-loader" name="file-loader" 
        multiple accept="audio/*" 
        onInput={onFileInput} 
        className="hidden"
        ref={fileInputRef}
        onClick={e => e.stopPropagation()}
      />
      <label 
        htmlFor="file-loader"
        onClick={e => e.stopPropagation()}
      >
        Drag and drop or click here to upload audio files.
      </label>
    </div>
  );
}