import { useSelector, useDispatch } from "react-redux";
import {
  setFullMix,
  selectReadyToRender,
  selectPlaying,
  selectFullMix
} from "./rendererSlice";

import {
  playTrackSequence,
  stopPlayback,
  renderTrackSequence
} from "./rendererAPI";

function FullPreviewSelector() {
  const dispatch = useDispatch();
  const fullMix = useSelector(selectFullMix);

  function onMixTypeChange(event) {
    dispatch(setFullMix(event.target.value === 'full'));
  }

  return (
    <div className={`
      w-2/5
    `}>
      <div>
        <input type="radio" id="full" name="mix" value="full" defaultChecked={fullMix} onChange={onMixTypeChange} />
        <label htmlFor="full">Full Mix</label>
      </div>
      <div>
        <input type="radio" id="preview" name="mix" value="preview" defaultChecked={!fullMix} onChange={onMixTypeChange} />
        <label htmlFor="preview">Preview Mix</label>
      </div>
    </div>
  );
}

export function Player() {
  const dispatch = useDispatch();

  const readyToRender = useSelector(selectReadyToRender);
  const isPlaying = useSelector(selectPlaying);

  function onClickPlay() {
    dispatch(playTrackSequence());
  }

  function onClickStop() {
    dispatch(stopPlayback());
  }

  function onClickDownload() {
    dispatch(renderTrackSequence());
  }

  return (
    <div className={`
      w-full py-2
      border-4 border-solid
      ${readyToRender ? 'border-gray-400' : 'border-gray-200'} 
      flex items-center justify-around
    `}>
      <div className={`
        w-3/5
        flex justify-center
        space-x-2
      `}>
        <button disabled={!readyToRender || isPlaying} onClick={onClickPlay}>Play</button>
        <button disabled={!(readyToRender && isPlaying)} onClick={onClickStop}>Stop</button>
        <button disabled={!readyToRender} onClick={onClickDownload}>Download</button>
      </div>
      <FullPreviewSelector />
   </div>
  );
}