import { createAsyncThunk } from '@reduxjs/toolkit';

import {
  setContextReady,
  setPlaying,
  selectContextReady,
  selectFullMix
} from "./rendererSlice";

import {
  selectFiles,
  selectMetaData,
  getAudioData
} from "../file-handler/fileHandlerSlice";

import {
  selectLoudness,
  selectFilterFreq,
  selectTempo
} from "../parameters/parametersSlice";

const audioContext = new (window.AudioContext || window.webkitAudioContext)();
const Kali = window.Kali;

const gainAttenuation = 0.75;
const fullMixFade = 10;
const previewMixFade = 5;
const previewMixDuration = 10;

let scheduledSourceNodes = [];

export const resumeAudioContext = createAsyncThunk(
  'renderer/resumeAudioContext',
  async (payload, { dispatch, getState }) => {
    if (selectContextReady(getState())) {
      return Promise.resolve();
    } else {
      dispatch(setContextReady(payload));
      return audioContext.resume();
    }
  }
);

export function decodeAudioFile(file) {
  return file.arrayBuffer()
    .then(ab => audioContext.decodeAudioData(ab));
}

function timeStretch(buffer, factor) {
  const channels = buffer.numberOfChannels;
  const block = 4096 * channels;
  const frames = buffer.length;
  const outFrames = Math.floor(frames/factor + 1);
  const outBuffer = audioContext.createBuffer(channels, outFrames, audioContext.sampleRate);

  // Process each channel separately. Multichannel processing in Kali requires interleaved samples, but AudioBuffers are not.
  for (let c = 0; c < channels; c++) {
    const kali = new Kali(1);
    kali.setup(audioContext.sampleRate, factor, false);
    let inOffset = 0, outOffset = 0;
    let flushed = false;

    const inputData = buffer.getChannelData(c);
    const processed = new Float32Array(outFrames);
  
    while (outOffset < processed.length) {
      outOffset += kali.output(processed.subarray(outOffset, Math.min(outOffset+block,processed.length)));
  
      if (inOffset < frames) {
        let data = inputData.subarray(inOffset, Math.min(inOffset+block, frames));
        inOffset += data.length;
        kali.input(data);
        kali.process();
      } else if (!flushed) {
        kali.flush();
        flushed = true;
      }
    }

    outBuffer.getChannelData(c).set(processed);
  }

  return outBuffer;
}

function partialTimeStretch(buffer, factor, segDuration=previewMixDuration) {
  const sampleRate = buffer.sampleRate;
  const channels = buffer.numberOfChannels;
  const srcSamplesNeeded = Math.ceil(buffer.sampleRate * segDuration * factor);

  const headBuffer = audioContext.createBuffer(channels, srcSamplesNeeded, sampleRate);
  const tailBuffer = audioContext.createBuffer(channels, srcSamplesNeeded, sampleRate);
  const end = buffer.length;

  for (let i = 0; i < channels; i++) {
    const srcChannelData = buffer.getChannelData(i);
    const headChannelData = headBuffer.getChannelData(i);
    const tailChannelData = tailBuffer.getChannelData(i);

    for (let j = 0; j < srcSamplesNeeded; j++) {
      headChannelData[j] = srcChannelData[j];
      tailChannelData[j] = srcChannelData[end-srcSamplesNeeded+j];
    }
  }

  const stretchedHeadBuffer = timeStretch(headBuffer, factor);
  const stretchedTailBuffer = timeStretch(tailBuffer, factor);

  return [stretchedHeadBuffer, stretchedTailBuffer];
}

function dbfsToAmp(dbFS) {
  return (dbFS === -96) ? 0 : Math.pow(10, dbFS/20);
}

function randomizeTracks(files) {
  let tracks = files.slice();
  for (let i = tracks.length-1; i > 0; i--) {
    const j = Math.floor(Math.random()*i);
    const temp = tracks[i];
    tracks[i] = tracks[j];
    tracks[j] = temp;
  }
  return tracks;
}

function preparePlaylist(state, fullMix) {
  const files = selectFiles(state);
  const tracks = randomizeTracks(files);
  const loudness = selectLoudness(state);
  const tgtBPM = selectTempo(state);
  const tgtLinearAmp = dbfsToAmp(loudness);
  let offset = 0;

  return tracks.flatMap((track, i) => {
    const metaData = selectMetaData(state)[track];
    const gainFactor = tgtLinearAmp / metaData.rms;
    const stretchFactor = tgtBPM / metaData.tempo;

    const buffer = getAudioData(track);
    const result = [];

    // The first and last tracks won't need crossfading
    const first = i === 0;
    const last = i === tracks.length-1;
          
    if (fullMix) {
      const stretchedBuffer = timeStretch(buffer, stretchFactor);
      const duration = stretchedBuffer.duration;
  
      result.push({
        name: track,
        buffer: stretchedBuffer,
        gain: gainFactor,
        offset, first, last
      });
  
      offset += duration-fullMixFade;
    } else {
      const [head, tail] = partialTimeStretch(buffer, stretchFactor);

      result.push({
        name: `${track} (head)`,
        buffer: head,
        gain: gainFactor,
        offset, 
        first,
        last: false // Head clips cannot be last
      }, {
        name: `${track} (tail)`,
        buffer: tail,
        gain: gainFactor,
        offset: offset+5,
        first: false, // Tail clips cannot be first
        last
      });

      offset += previewMixDuration;
    }

    return result;
  });
}

function schedulePlayback(context, buffer, now, offset, gain, cof, rise, decay) {
  const source = context.createBufferSource();
  const filter = context.createBiquadFilter();
  const amp = context.createGain();

  source.buffer = buffer;
  filter.type = 'highpass';
  filter.frequency.value = cof;

  gain *= gainAttenuation;

  const duration = buffer.duration;
  amp.gain.setValueAtTime(rise ? 0 : gain, now+offset);
  rise && amp.gain.setTargetAtTime(gain, now+offset, rise);
  decay && amp.gain.setTargetAtTime(0+Number.EPSILON, now+offset+duration-decay, decay);

  source.connect(filter);
  filter.connect(amp);
  amp.connect(context.destination);
  source.start(now+offset);

  return source;
}

export const playTrackSequence = createAsyncThunk(
  'renderer/playTrackSequence',
  (_, { dispatch, getState }) => {
    const state = getState();
    const filterFreq = selectFilterFreq(state);
    const fullMix = selectFullMix(state);
    const playlist = preparePlaylist(state, fullMix);

    scheduledSourceNodes = playlist.map(data => {
      const fade = fullMix ? fullMixFade : previewMixFade;
      const now = audioContext.currentTime;
  
      return schedulePlayback(
        audioContext,
        data.buffer,
        now, data.offset,
        data.gain,
        filterFreq,
        !data.first * fade,
        !data.last * fade
      );
    });

    const lastNode = scheduledSourceNodes[scheduledSourceNodes.length-1];
    lastNode.onended = function () {
      dispatch(setPlaying(false));
    };

    dispatch(setPlaying(true));
  }
);

export const stopPlayback = createAsyncThunk(
  'renderer/stopPlayback',
  (_, { dispatch }) => {
    scheduledSourceNodes.forEach(node => node.stop());
    scheduledSourceNodes = [];
    dispatch(setPlaying(false));
  }
);

export const renderTrackSequence = createAsyncThunk(
  'renderer/renderTrackSequence',
  (_, { getState }) => {
    const state = getState();
    const filterFreq = selectFilterFreq(state);
    const fullMix = selectFullMix(state);
    const playlist = preparePlaylist(state, fullMix);

    const totalDurationInSamps = playlist.reduce((a,b) => {
      const { buffer, last } = b;
      // Note: A risky assumption that file sampling rates are always 44.1k Hz...
      return a + buffer.length - (last ? 0 : 44100 * (fullMix ? fullMixFade : previewMixFade));
    }, 0);

    // Note: Assumes stereo 44.1k Hz for all tracks...
    const offlineContext = new OfflineAudioContext(2, totalDurationInSamps, 44100);

    playlist.forEach(data => {
      const fade = fullMix ? fullMixFade : previewMixFade;
      const now = offlineContext.currentTime;
  
      schedulePlayback(
        offlineContext,
        data.buffer,
        now, data.offset,
        data.gain,
        filterFreq,
        !data.first * fade,
        !data.last * fade
      );
    });
    
    offlineContext.startRendering().then(buffer => {
      const wav = bufferToWav(buffer);
      download(wav);
    });
  }
);

function download(wav) {
  const url = URL.createObjectURL(wav);
  const a = document.createElement('a');
  document.body.appendChild(a);
  a.style = 'display:none';
  a.href = url;
  a.download = 'djmix.wav';
  a.click();
  URL.revokeObjectURL(url);
  document.body.removeChild(a);
}

function bufferToWav(buffer) {
  let leftPCM, rightPCM;

  if (buffer.numberOfChannels === 2) {
    leftPCM = buffer.getChannelData(0);
    rightPCM = buffer.getChannelData(1);
  } else {
    leftPCM = buffer.getChannelData(0);
    rightPCM = buffer.getChannelData(0);
  }

  const interleaved = interleave(leftPCM, rightPCM);
  const dataview = encodeWav(interleaved, buffer.sampleRate);
  return new Blob([dataview], { type: 'audio/wav' });
}

function interleave(inputL, inputR) {
  const length = inputL.length + inputR.length;
  const result = new Float32Array(length);

  let index = 0, inputIndex = 0;

  while (index < length) {
    result[index++] = inputL[inputIndex];
    result[index++] = inputR[inputIndex];
    inputIndex++;
  }
  return result;
}

function encodeWav(samples, sampleRate) {
  const buffer = new ArrayBuffer(44 + samples.length * 2);
  const view = new DataView(buffer);

  /* RIFF identifier */
  writeString(view, 0, 'RIFF');
  /* file length */
  view.setUint32(4, 32 + samples.length * 2, true);
  /* RIFF type */
  writeString(view, 8, 'WAVE');
  /* format chunk identifier */
  writeString(view, 12, 'fmt ');
  /* format chunk length */
  view.setUint32(16, 16, true);
  /* sample format (raw) */
  view.setUint16(20, 1, true);
  /* channel count */
  view.setUint16(22, 2, true);
  /* sample rate */
  view.setUint32(24, sampleRate, true);
  /* byte rate (sample rate * block align) */
  view.setUint32(28, sampleRate * 4, true);
  /* block align (channel count * bytes per sample) */
  view.setUint16(32, 4, true);
  /* bits per sample */
  view.setUint16(34, 16, true);
  /* data chunk identifier */
  writeString(view, 36, 'data');
  /* data chunk length */
  view.setUint32(40, samples.length * 2, true);

  floatTo16BitPCM(view, 44, samples);

  return view;
}

function floatTo16BitPCM(output, offset, input) {
  for (let i = 0; i < input.length; i++, offset += 2) {
    const s = Math.max(-1, Math.min(1, input[i]));
    output.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7FFF, true);
  }
}

function writeString(view, offset, string) {
  for (let i = 0; i < string.length; i++) {
    view.setUint8(offset + i, string.charCodeAt(i));
  }
}
