import { useSelector, useDispatch } from "react-redux";

import {
  setPartialScan,
  selectPartialScan
} from "./analyzerSlice";

import { selectReadyToAnalyze } from "../file-handler/fileHandlerSlice";
import { callAnalyzer } from "./analyzerAPI";

export function Analyzer() {
  const dispatch = useDispatch();
  const readyToAnalyze = useSelector(selectReadyToAnalyze);
  const partialScan = useSelector(selectPartialScan);

  function onPartialScanChange(event) {
    dispatch(setPartialScan(event.target.checked));
  }

  return (
    <div className={`
      flex items-center justify-evenly
      border-4 border-solid 
      ${readyToAnalyze ? 'border-gray-400' : 'border-gray-200'}
      w-full py-2
    `}>
      <button disabled={!readyToAnalyze} onClick={() => dispatch(callAnalyzer())}>Analyze</button>
      <div>
        <input type="checkbox" id="partial-scan" defaultChecked={partialScan} onChange={onPartialScanChange} />
        <label htmlFor="partial-scan">Partial Scan</label>
      </div>
    </div>
  );
}