import { createAsyncThunk } from '@reduxjs/toolkit';

import {
  addFile,
  setAudioData
} from "./fileHandlerSlice";

import { decodeAudioFile } from "../renderer/rendererAPI";

export const processFiles = createAsyncThunk(
  'fileHandler/processFiles',
  async (files, { dispatch }) => {
    if (!files && files.length === 0) {
      return Promise.reject();
    } else if (files.length === 1) {
      const file = files[0];
      return decodeAudioFile(file).then(buffer => {
        dispatch(addFile(file.name));
        setAudioData(file.name, buffer);
      });
    } else {
      const filesArray = Array.from(files);
      return Promise.all(filesArray.map(file => {
        return decodeAudioFile(file).then(buffer => {
          dispatch(addFile(file.name));
          setAudioData(file.name, buffer);
        });
      }))
    }
  }
);