import { createSlice } from '@reduxjs/toolkit';

export const rendererSlice = createSlice({
  name: 'renderer',
  initialState: {
    contextReady: false,
    readyToRender: false,
    playing: false,
    fullMix: false
  },
  reducers: {
    setContextReady(state, { payload }) {
      state.contextReady = payload;
    },
    setReadyToRender(state, { payload }) {
      state.readyToRender = payload;
    },
    setPlaying(state, { payload }) {
      state.playing = payload;
    },
    setFullMix(state, { payload }) {
      state.fullMix = payload;
    }
  }
});

export const {
  setContextReady,
  setReadyToRender,
  setPlaying,
  setFullMix
} = rendererSlice.actions;

export const selectContextReady = (state) => state.renderer.contextReady;
export const selectReadyToRender = (state) => state.renderer.readyToRender;
export const selectPlaying = (state) => state.renderer.playing;
export const selectFullMix = (state) => state.renderer.fullMix;

export default rendererSlice.reducer;
