import { createSlice } from '@reduxjs/toolkit';

export const parametersSlice = createSlice({
  name: 'parameters',
  initialState: {
    loudness: -12,
    filterFreq: 0,
    tempo: 120,
    averageTempo: 120,
    useAverageTempo: true
  },
  reducers: {
    setLoudness(state, { payload }) {
      state.loudness = payload;
    },
    setFilterFreq(state, { payload }) {
      state.filterFreq = payload;
    },
    setTempo(state, { payload }) {
      state.tempo = payload;
    },
    setAverageTempo(state, { payload }) {
      state.averageTempo = payload;
    },
    setUseAverageTempo(state, { payload }) {
      state.useAverageTempo = payload;
    }
  }
});

export const {
  setLoudness,
  setFilterFreq,
  setTempo,
  setAverageTempo,
  setUseAverageTempo
} = parametersSlice.actions;

export const selectLoudness = (state) => state.parameters.loudness;
export const selectFilterFreq = (state) => state.parameters.filterFreq;
export const selectTempo = (state) => state.parameters.tempo;
export const selectAverageTempo = (state) => state.parameters.averageTempo;
export const selectUseAverageTempo = (state) => state.parameters.useAverageTempo;

export default parametersSlice.reducer;